:root {
  --color-white: #ffffff;
  --color-black: #010101;
  --color-green: #4caf50;
  --color-red: #f44336;
  --color-blue: #2196f3;
  --color-orange: #f3aa21;
  --color-sweet: #db21f3;
  --color-blue: #2196f3;

  --color-primary-text: #212121;
  --color-secondary-text: #757575;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ecf0f3;
  align-items: center;
  text-align: center;
  justify-content: center;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 10px;
}

ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
}
